import React from 'react';
import './Galeria.css'
import ImageGallery from 'react-image-gallery';
import portada from '../../assets/img/horarios/PORTADA.jpg'
import CLUB_VIRTUAL from '../../assets/img/horarios/CLUB-VIRTUAL.jpg'
import HORARIO_DOMINGO from '../../assets/img/horarios/HORARIO-DOMINGO.jpg'
import HORARIO_SABADO from '../../assets/img/horarios/HORARIO-SABADO.jpg'

const images = [
    {
        original: portada,
    },
    {
        original: CLUB_VIRTUAL,
    },
    {
        original: HORARIO_DOMINGO,
    },
    {
        original: HORARIO_SABADO,
    },
];

class Galeria extends React.Component {

    render() {

        return (
            <div>
                <ImageGallery items={images} />
            </div>
        )
    }

}

export default Galeria;